import React from "react"
import { useQuery, keepPreviousData } from '@tanstack/react-query'
import CircularProgress from '@mui/material/CircularProgress'
import style from './JobStatus.module.css'
import { DEFAULT_WIDGET_CONFIG } from "../default-widget-config"
import axios from "axios"

const JOB_STATUS_ORDER = [ "RUNNING", "QUEUED" ] as const
type JobStatus = typeof JOB_STATUS_ORDER[number]

type JobStatusCounts = {
  [k in JobStatus]: number
}

type JobStatusConfig = {
  statusTextTemplateString: string
  zeroJobsString: string
  statusLabels: Record<JobStatus, string>
  initiatorTagsToInclude: string | null | undefined
  refetchInterval: string | null | undefined
}

export default function JobStatus(props: {
  widgetId: string
} & JobStatusConfig) {
  const refetchInterval = Math.max(1000,
    parseFloat(props.refetchInterval || DEFAULT_WIDGET_CONFIG.JobStatus.refetchInterval) * 1000)

  const jobStatusResult = useQuery<JobStatusCounts>({
    queryKey: ['job-status', props.widgetId],
    queryFn: () => {
      const initiatorTagsToInclude = props.initiatorTagsToInclude ? props.initiatorTagsToInclude.split(',') : null
      return fetchJobStatus(initiatorTagsToInclude)
    },
    placeholderData: keepPreviousData,
    refetchInterval
  })

  if (jobStatusResult.isFetching && !jobStatusResult.data) {
    return <div className={style.status}><CircularProgress size={20} /></div>
  }

  if (jobStatusResult.isError || !jobStatusResult.data) {
    return (
      <div className={style.status}>
        Error retrieving job status.
      </div>
    )
  }

  const jobStatus = jobStatusResult.data
  const numJobs = Object.values(jobStatusResult.data).reduce((acc, count) => acc + count, 0)
  if (numJobs === 0) {
    return <div className={style.status}>{props.zeroJobsString || ""}</div>
  }

  return (
    <div className={style.status}>
      <CircularProgress size={20} color="inherit" />
      <JobStatusParts jobStatus={jobStatus} config={props} />
    </div>
  )
}

function JobStatusParts(props: {
  jobStatus: JobStatusCounts,
  config: JobStatusConfig
}) {
  const defaultStatusLabels = DEFAULT_WIDGET_CONFIG.JobStatus.statusLabels
  const statusLabels = props.config.statusLabels ?? defaultStatusLabels
  const statusTextTemplateString = props.config.statusTextTemplateString ?? DEFAULT_WIDGET_CONFIG.JobStatus.statusTextTemplateString

  return (
    <div>
      {addElementInBetween(
        ', ',
        JOB_STATUS_ORDER
          .filter(status => status in props.jobStatus)
          .filter(status => props.jobStatus[status] > 0)
          .map(status => (
            <span key={status} dangerouslySetInnerHTML={{
              __html: createStatusTextHtmlString(
                statusTextTemplateString,
                statusLabels[status] || defaultStatusLabels[status],
                props.jobStatus[status]
              )
            }} />
          ))
      )}
    </div>
  )

  function createStatusTextHtmlString(templateString: string, statusLabel: string, count: number) {
    let htmlString = templateString
    htmlString = htmlString.replace('{{.status}}', statusLabel)
    htmlString = htmlString.replace('{{.count}}', String(count))
    return htmlString
  }
}

async function fetchJobStatus(initiatorTagsToInclude: string[] | null): Promise<JobStatusCounts> {
  const response = await axios.post<JobStatusCounts>(`/servlets/cgi/api/job-status`, {
    initiatorTagsToInclude
  },{
    headers: {
      'Accept': 'application/json'
    }
  })
  return response.data
}

function addElementInBetween<T extends JSX.Element, B extends React.ReactNode>(inBetween: B, array: T[]) {
  return array.flatMap((e, index) => {
    return index === array.length - 1
      ? [ e ]
      : [ e, <React.Fragment key={`${e.key}--separator`}>{inBetween}</React.Fragment> ]
  })
}
